import { useState } from 'react';

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLock } from "@fortawesome/free-solid-svg-icons";
import { Container, Row, Col, Modal, Button, Form } from "react-bootstrap";

import { logIn, logOut } from '../firebase/config.js';

const Login = (props) => {

    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleOpen = () => setShow(true);

    const [showErr, setShowErr] = useState(false);

    const [email, setEmail] = useState('');
    const [pw, setPw] = useState('');

    const submitForm = (e) => { 
        e.preventDefault();

        setShowErr(false);

        logIn(email, pw)
        .then((cred) => {
            props.setUser(cred.user);
            handleClose();
        })
        .catch((error) => {
            setShowErr(true);
        });

        setEmail('');
        setPw('');
    }

    const logoutFn = () => {
        logOut().then(() => {
            props.setUser(null);
            handleClose();
        }).catch((error) => {
            console.log(error.code, error.message);
        });
    }

    return (
        <>

        <Container fluid style={{backgroundColor:'#00239C'}}>
            <Container>
                <Row className='pb-2'>
                    <Col style={{cursor: 'pointer'}} onClick={handleOpen}>
                        {props.user === null ?
                        <h7 style={{color: '#E10600', paddingLeft:'10px', paddingRight:'10px'}}>Login</h7>
                        :
                        <h7 style={{color: '#E10600', paddingLeft:'10px', paddingRight:'10px'}}>Logout</h7> }

                        <FontAwesomeIcon icon={faLock} color='#E10600' />

                        {props.user !== null && <h7 style={{color: '#E10600', paddingLeft:'20px', paddingRight:'10px'}}>Loggged in as: ADMIN</h7>}
                    </Col>
                </Row>
            </Container>
        </Container>

        { props.user === null ?
        <Modal show={show} onHide={handleClose} size="lg" centered style={{backgroundColor: 'rgba(0,0,0,0.4)'}}>

            <Form onSubmit={submitForm}>
            <Modal.Header closeButton>
                <Modal.Title>Admin login</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Form.Group className="mb-3">
                    <Form.Label>Email address</Form.Label>
                    <Form.Control type="email" placeholder="Enter email" onChange={(e) => setEmail(e.target.value)} value={email} required/>
                </Form.Group>

                <Form.Group className="mb-3">
                    <Form.Label>Password</Form.Label>
                    <Form.Control type="password" placeholder="Password" onChange={(e) => setPw(e.target.value)} value={pw} required/>
                </Form.Group>

                {showErr && <Form.Label style={{color:'red', fontSize:'13px'}}>Email e/o password errati</Form.Label>}
            </Modal.Body>
            <Modal.Footer>
                <Row>
                    <Col><Button style={{backgroundColor: '#00239C'}} type="submit">Login</Button></Col>
                    <Col><Button style={{backgroundColor: '#00239C'}} onClick={handleClose}>Chiudi</Button></Col>
                </Row>
                
            </Modal.Footer>
            </Form>

        </Modal>
        :
        <Modal show={show} onHide={handleClose} size="lg" centered style={{backgroundColor: 'rgba(0,0,0,0.4)'}}>

            <Modal.Header closeButton>
                <Modal.Title>Admin login</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                Sei sicuro di voler uscire?
            </Modal.Body>
            <Modal.Footer>
                <Row>
                    <Col><Button style={{backgroundColor: '#00239C'}} onClick={logoutFn}>Logout</Button></Col>
                    <Col><Button style={{backgroundColor: '#00239C'}} onClick={handleClose}>Chiudi</Button></Col>
                </Row>
                
            </Modal.Footer>

        </Modal>
        }

        </>
    );
}

export default Login;

