import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPaperPlane, faEnvelope, faPhone, faPlus, faTrashAlt } from "@fortawesome/free-solid-svg-icons";

import { useEffect, useState } from "react";
import { Container, Row, Col, Card, Button, Modal, Form } from "react-bootstrap";

import useWindowSize from '../useWindowSize.js';

import FsLightbox from "fslightbox-react";

import './Header.css';

import { setDoc, doc } from "@firebase/firestore";
import { projectFirestore as db } from "../firebase/config.js";
import { projectStorage } from '../firebase/config';
import { ref, uploadBytesResumable, getDownloadURL, deleteObject } from 'firebase/storage';

const ModalDelete = (props) => {

    const handleClose = () => {
        props.setShow(false);
    }

    const handleDelete = () => {

        let dataList = props.quadri.filter((el) => {
            if (el.src === props.element.src && el.title === props.element.title && el.artist === props.element.artist)
                return false;
            else
                return true;
        });
        
        props.setQuadri(dataList);
        dataList = dataList.map((el) => JSON.stringify(el));

        let filename = props.element.src.split('https://firebasestorage.googleapis.com/v0/b/flashpoint-art-24f12.appspot.com/o/')[1];
        filename = filename.split('?')[0];

        const storageRef = ref(projectStorage, filename);

        deleteObject(storageRef).then( async () => {
            await setDoc(doc(db, "images", "dfh2arTGft6pItFNZbnS"), {list: dataList});
            handleClose();
        }).catch( async (err) => {
            await setDoc(doc(db, "images", "dfh2arTGft6pItFNZbnS"), {list: dataList});
            handleClose();
        });
    }

    return (
        <Modal show={props.show} onHide={handleClose} size="lg" centered style={{backgroundColor: 'rgba(0,0,0,0.4)'}}>
            <Modal.Header>
                <Modal.Title>Elimina</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                Sicuro di voler eliminare il quadro?
            </Modal.Body>
            <Modal.Footer>
                <Button variant='danger' onClick={handleDelete}>Elimina</Button>
                <Button style={{backgroundColor: '#00239C'}} onClick={handleClose}>Chiudi</Button>
            </Modal.Footer>
        </Modal>
    );
}

const Quadro = (props) => {

    const size = useWindowSize();
    
    const [toggler, setToggler] = useState(false);

    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleOpen = () => setShow(true);

    const handleClick = () => {
        setToggler(o => !o);
    }

    const [showDel, setShowDel] = useState(false);
    const handleOpenDel = () => setShowDel(true);

    return(
        <>
        <Col className={size.width > 973 ? 'mb-4' : 'mb-4 d-flex justify-content-center'}>

        <FsLightbox toggler={toggler} sources={[props.element.src]} types={['image']} />

        <Card style={{ width: '20rem', minHeight:'35rem'}}>
            <Card.Img onClick={handleClick} variant="top" src={props.element.src} style={ size.width > 975 ? {width: '100%', height: '12vw',objectFit:'cover', cursor: 'pointer'} : {cursor: 'pointer'}} />
            <Card.Body>
                <Card.Title className='mb-3 mt-3 d-flex'>
                    {props.user !== null && <FontAwesomeIcon icon={faTrashAlt} color='black' style={{marginRight:'15px', cursor:'pointer'}} onClick={handleOpenDel}/>}
                    <h5>{props.element.title}</h5>
                </Card.Title>
                <Card.Text className='d-flex justify-content-center' style={{flexDirection: 'column', fontSize:'0.9em'}}>
                    <h7 className='pb-3'>by {props.element.artist}</h7>
                    <h7 className='pb-2'>Dimensione (cm): {props.element.width} x {props.element.height}</h7>
                    <h7 className='pb-2'>Autenticità: {props.element.auth}</h7>
                    <h7 className='pb-2'>Anno: {props.element.year}</h7>
                    <h7 className='pb-3'>Tecnica: {props.element.tech}</h7>
                    <Button style={{backgroundColor: '#00239C'}} onClick={handleOpen} className='d-flex justify-content-center align-items-center'>
                        <h7 style={{marginRight: '10px'}}>Contatta ora</h7>
                        <FontAwesomeIcon icon={faPaperPlane} color={'white'}/> 
                    </Button>
                </Card.Text>
                    
            </Card.Body>
        </Card>
        </Col>

        <ModalDelete show={showDel} setShow={setShowDel} quadri={props.quadri} setQuadri={props.setQuadri} element={props.element} />

        <Modal show={show} onHide={handleClose} size="lg" centered style={{backgroundColor: 'rgba(0,0,0,0.4)'}}>
            <Modal.Header>
                <Modal.Title>Contatti</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <Col className='d-flex align-items-center'>
                    <FontAwesomeIcon icon={faEnvelope} color='black' />
                    <h7 className='mb-4 mt-4' style={{marginLeft:'20px'}}>Email: flashpointartshop@gmail.com</h7>
                    </Col>
                </Row>
                <Row>
                    <Col className='d-flex align-items-center'>
                    <FontAwesomeIcon icon={faPhone} color='black' />
                    <h7 className='mb-4 mt-4' style={{ marginLeft:'20px'}}>Whatsapp: +39 339 107 6327</h7>
                    </Col>
                </Row>
            </Modal.Body>
            <Modal.Footer>
                <Button style={{backgroundColor: '#00239C'}} onClick={handleClose}>Chiudi</Button>
            </Modal.Footer>
        </Modal>
        </>
    );
}

const ProgressBar = ({ progress }) => {
    return (
        <div style={{borderRadius:'3px 3px 0px 0px', width: progress.toString() + '%', height:'5px', backgroundColor:'#E10600', position:'relative', zIndex:'3', top:'0', left:'0'}}></div>
    );
}

const ModalAdd = (props) => {

    const [title, setTitle] = useState('');
    const [artist, setArtist] = useState('');
    const [width, setWidth] = useState(0);
    const [height, setHeight] = useState(0);
    const [auth, setAuth] = useState('');
    const [year, setYear] = useState('');
    const [tech, setTech] = useState('');
    const [file, setFile] = useState(null);

    const [upload, setUpload] = useState(false);
    const [prog, setProg] = useState(0);

    const [fileErr, setFileErr] = useState(false);

    const handleClose = () => {
        setTitle('');
        setArtist('');
        setWidth(0);
        setHeight(0);
        setAuth('');
        setYear('');
        setTech('');
        setFile(null);
        setUpload(false);
        props.setShow(false);
    }

    const submitNewQuadro = async (e) => {
        e.preventDefault();
        setUpload(true);

        //const { url, progress } = useStorage(file);
        
        // write to cloud storage
        const storageRef = ref(projectStorage, file.name);

        const uploadTask = uploadBytesResumable(storageRef, file);

        uploadTask.on('state_changed', (snap) => {
            let percentage = (snap.bytesTransferred / snap.totalBytes) * 100;
            setProg(percentage);
        }, (err) => {
            console.log(err);
        }, async () => {
            const url = await getDownloadURL(uploadTask.snapshot.ref);
            
            // write to firestore
            const dataList = [];
            props.quadri.forEach((el) => {
                dataList.push(JSON.stringify(el));
            })
            dataList.push(JSON.stringify({title: title, artist: artist, width: width, height: height, auth: auth, year: year, tech: tech, src: url}))

            await setDoc(doc(db, "images", "dfh2arTGft6pItFNZbnS"), {list: dataList});

            // load changes locally to limit reads
            props.setQuadri(o => [...o, {title: title, artist: artist, width: width, height: height, auth: auth, year: year, tech: tech, src: url}]);
        
            handleClose();
        });
    }

    const fileHandler = (e) => {
        setFileErr(false);
        let selected = e.target.files[0];

        if (selected && selected.type.startsWith('image')) {
            console.log(selected);
            setFile(selected);
        } else {
            setFile(null);
            setFileErr(true);
        }
    }

    return (
        <Modal show={props.show} onHide={handleClose} size="lg" centered style={{backgroundColor: 'rgba(0,0,0,0.4)'}} backdrop='static'>
            { upload && <ProgressBar progress={prog} /> }

            <Form onSubmit={submitNewQuadro}>
            <Modal.Header closeButton>
                <Modal.Title>Aggiungi un quadro</Modal.Title>
            </Modal.Header>
            <Modal.Body>

                <Form.Group className="mb-2">
                    <Form.Label>Titolo del quadro</Form.Label>
                    <Form.Control type="text" required onChange={(e) => setTitle(e.target.value)} value={title}/>
                </Form.Group>

                <Form.Group className="mb-2">
                    <Form.Label>Artista</Form.Label>
                    <Form.Control type="text" required onChange={(e) => setArtist(e.target.value)} value={artist}/>
                </Form.Group>
                
                <Form.Group className="mb-2">
                    <Form.Label>Dimensione (cm)</Form.Label>
                    <Row>
                        <Col><Form.Control type="number" min='1' required onChange={(e) => setWidth(e.target.value)} value={width}/></Col>
                        <Col xs={1} className='d-flex justify-content-center align-items-center'>x</Col>
                        <Col><Form.Control type="number" min='1' required onChange={(e) => setHeight(e.target.value)} value={height}/></Col>
                    </Row>
                </Form.Group>

                <Form.Group className="mb-2">
                    <Form.Label>Autenticità</Form.Label>
                    <Form.Control type="text" required onChange={(e) => setAuth(e.target.value)} value={auth}/>
                </Form.Group>

                <Form.Group className="mb-2">
                    <Form.Label>Anno</Form.Label>
                    <Form.Control type="text" required onChange={(e) => setYear(e.target.value)} value={year}/>
                </Form.Group>

                <Form.Group className="mb-2">
                    <Form.Label>Tecnica</Form.Label>
                    <Form.Control type="text" required onChange={(e) => setTech(e.target.value)} value={tech}/>
                </Form.Group>

                <Form.Group className="mb-2">
                    <Form.Label>Foto</Form.Label>
                    <Form.Control type="file" required onChange={fileHandler} />
                    {fileErr && <Form.Label style={{color:'red', fontSize:'13px'}}>Seleziona un'immagine</Form.Label>}
                </Form.Group>

            </Modal.Body>

            <Modal.Footer>
                <Button style={{backgroundColor: '#00239C'}} type='submit'>Aggiungi</Button>
                <Button style={{backgroundColor: '#00239C'}} onClick={handleClose}>Chiudi</Button>
            </Modal.Footer>
            </Form>

        </Modal>
    );
}

const Shop = (props) => {

    const [paintings, setPaintings] = useState([]);
    const [artists, setArtists] = useState([]);

    //const [update, setUpdate] = useState(false);

    const [show, setShow] = useState(false);
    const handleOpen = () => setShow(true);

    const QUADRI = props.quadri;

    useEffect(() => {
        setPaintings([...QUADRI]);
        setArtists([...new Set(QUADRI.map(el => el.artist))].sort());
    }, [QUADRI]);

    const filterPaintings = (artist) => {
        setPaintings(QUADRI.filter((el) => el.artist === artist));
    }

    const resetPaintings = () => {
        setPaintings([...QUADRI]);
    }

    return (
        <>

        <Container fluid style={{backgroundColor: '#fff', minHeight: '60vh'}} className='pb-5 pt-5'>
            <Container>
                <Row>
                    <Col md={2} className='mb-4'>
                        <Row><h3 className='mb-4' style={{color: 'black'}}>Artisti</h3></Row>
                        <Row onClick={resetPaintings}><u><h6 className='mb-3 hl-hover' >Tutti</h6></u></Row>

                        {artists.map((el, i) => <Row key={i} onClick={() => filterPaintings(el)}><u><h6 className='mb-3 hl-hover'>{el}</h6></u></Row>)}

                    </Col>
                    <Col md={10}>
                        <Row>
                            <Col className='d-flex align-items-baseline'>
                            <h3 className='mb-4' style={{color: 'black'}}>Shop</h3>
                            {props.user !== null && <FontAwesomeIcon icon={faPlus} style={{marginLeft:'20px', cursor:'pointer'}} color='black' onClick={handleOpen} />}
                            </Col>
                        </Row>

                        <Row className='g-3'>
                        {paintings.map((el, i) => <Quadro key={i} element={el} idx={i} user={props.user} quadri={props.quadri} setQuadri={props.setQuadri} />)}
                        </Row>
                    </Col>
                </Row>
            </Container>
        </Container>

        { props.user !== null &&
        <ModalAdd show={show} setShow={setShow} quadri={props.quadri} setQuadri={props.setQuadri}/>
        }
        </>
    );
}

export default Shop;