import { Col, Container, Row } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelope, faPhone } from "@fortawesome/free-solid-svg-icons";


const Contatti = () => {

    return (
        <Container fluid style={{backgroundColor: '#001c80', minHeight: '60vh'}}>
            <Container>
                <Row>
                    <Col>
                        <h1 className='mt-5 mb-5' style={{fontFamily: 'Montserrat', fontSize: 'bold'}}>Contatti</h1>
                    </Col>
                </Row>
                <Row className='d-flex align-items-center mb-5'>
                    <Col md className='d-flex align-items-center'>
                        <FontAwesomeIcon icon={faEnvelope} color='#e10600' size='2x' />
                        <h3 className='mb-4 mt-4' style={{marginLeft:'20px', color: '#e10600', fontFamily: 'Montserrat'}}>Email:</h3>
                    </Col>
                    <Col md>
                        <h5 className='mb-4 mt-4' style={{marginLeft:'20px', color: '#e10600', fontFamily: 'Montserrat'}}>flashpointartshop@gmail.com</h5> 
                    </Col>
                </Row>
                <Row className='d-flex align-items-center pb-5'>
                    <Col md className='d-flex align-items-center'>
                        <FontAwesomeIcon icon={faPhone} color='#e10600' size='2x' />
                        <h3 className='mb-4 mt-4' style={{marginLeft:'20px', color: '#e10600', fontFamily: 'Montserrat'}}>Whatsapp:</h3>
                    </Col>
                    <Col md>
                        <h5 className='mb-4 mt-4' style={{marginLeft:'20px', color: '#e10600', fontFamily: 'Montserrat'}}>+39 339 107 6327</h5>
                    </Col>
                </Row>
            </Container>
        </Container>
    );
}

export default Contatti;