
import { Container, Row, Col } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import useWindowSize from '../useWindowSize';

import './Header.css';

const Header = () => {

    const size = useWindowSize();

    return(
        <Container fluid style={{backgroundColor: '#00239C'}}>
            <Container>
                <Row>
                    <Col className='d-flex justify-content-center mt-5 mb-5 text-center'>
                        {/*<h1 style={{fontSize: '80px'}}>FLASHPOINT ART</h1>*/}
                        { size.width > 751 ? 
                        <img src={process.env.PUBLIC_URL + 'title.png'} alt='' style={{width: '65vw', height: '14vh'}} />
                        :
                        <img src={process.env.PUBLIC_URL + 'titlemob.png'} alt='' style={{width: '80vw', height: '14vh'}} />
                        }
                    </Col>
                </Row>
                <Row className='pb-5'>
                    <Col md className='mb-3 d-flex justify-content-center'><Link to='/' style={{textDecoration: 'none'}}>
                        {/*<h6 className='text-center'>HOME</h6>*/}
                        <img src={process.env.PUBLIC_URL + 'home.png'} alt='' style={{width: size.width > 751 ? '8vw' : '25vw', height: 'auto'}} />
                    </Link></Col>
                    <Col md className='mb-3 d-flex justify-content-center'><Link to='/artists' style={{textDecoration: 'none'}}>
                        {/*<h6 className='text-center'>ARTISTI</h6>*/}
                        <img src={process.env.PUBLIC_URL + 'artisti.png'} alt='' style={{width: size.width > 751 ? '8vw' : '25vw', height: 'auto'}} />
                    </Link></Col>
                    <Col md className='d-flex justify-content-center'><Link to='/contacts' style={{textDecoration: 'none'}}>
                        {/*<h6 className='text-center'>CONTATTI</h6>*/}
                        <img src={process.env.PUBLIC_URL + 'contatti.png'} alt='' style={{width: size.width > 751 ? '8vw' : '25vw', height: 'auto'}} />
                    </Link></Col>
                </Row>
            </Container>
        </Container>
    );
}

export default Header;